.gallery-title {
  text-align: center;
}

.gallery-description {
  text-align: center;
  max-width: 80%;
  margin: 0 auto;
  margin-bottom: 20px;
}

.gallery-grid {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.gallery-column {
  width: 33%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.small-image {
  padding: 10px;
  width: 90%;
}

.small-image.hover {
  transform: scale(1.05);
  transition: transform 0.3s ease;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 998;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: opacity 0.3s;
  flex-direction: row;
}

.button {
  transition: transform 0.3s ease;
  transform: scale(0.6);
}

.button:hover {
  transform: scale(0.8);
}

.prev-button,
.next-button {
  position: fixed;
  top: 50%;
  z-index: 1000;
}

.prev-button {
  left: 2vw;
}

.next-button {
  right: 2vw;
}

.modal-content {
  max-width: 80vw;
  max-height: 80vh;
  overflow: auto;
  position: relative;
  display: flex;
  align-items: center;
}

.prev-button,
.next-button {
  position: absolute;
  margin: 0 20px;
}

.close-button.button{
  position: fixed;
  top: 5vw;
  left: 5vw;
  font-size: 5vw;
  z-index: 1000;
  color: white;
}

.modal-image {
  flex: 1;
}

.modal-image img {
  width: 100%;
}

.small-image {
  transition: all 0.6s ease;
}

.small-image:hover {
  transform: scale(1.05);
}

.gallery {
  margin-top: 4vw;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0.6vw;
  width: 100%;
  z-index: 999;
}

.thumbnail {
  width: 50px;
  margin: 0 5px;
  opacity: 0.6;
  transition: all 0.3s;
}

.thumbnail.active-thumbnail {
  opacity: 1;
  scale: 1.2;
}

.thumbnail:hover {
  opacity: 1;
  scale: 1.2;
}

@media (max-width: 768px) {
  .prev-button, .next-button {
    display: none;
  }

  .gallery-grid {
    flex-direction: column;
    align-items: center;
  }

  .gallery-column {
    width: 100%;
  }

  .dot-thumbnail {
    width: 2vw;
    height: 2vw;
    border-radius: 50%;
    background-color: gray;
    margin: 1vw;
    margin-bottom: 16vh;
  }

  .dot-thumbnail.active-thumbnail {
    background-color: white;
  }

  .gallery-title{
    font-size: 6vw;
  }

  .gallery-description{
    display: none;
  }

  .close-button {
    position: fixed;
    top: 10vh;
    right: 10px;
    background-color: #fff;
    border: none;
    padding: 10px;
    border-radius: 2vw;
    cursor: pointer;
    z-index: 999;
    transition: all 0.3s;
  }

  .close-button:hover{
    background-color: #a0a0a0;
  }
}

/* Desktop-specific styles */
@media (min-width: 769px) {
  .close-button {
    display: none; /* Hide close button on desktop */
  }
}

.modal-image {
  transition: all 0.3s ease;
  overflow: hidden;
}

.modal-image.left img {
  animation: swipeLeft 0.3s ease forwards;
}

.modal-image.right img {
  animation: swipeRight 0.3s ease forwards;
}

@keyframes swipeLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes swipeRight {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
